import React from "react";
import classes from './Dialogs.module.css';
import DialogItem from "./DialogItem/DialogItem";
import MessageItem from "./MessageItem/MessageItem";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../../common/FormsControl/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";

const Dialogs = (props) => {

    let nameElements = props.store.names.map(name =>
        <DialogItem name={name.name} key={name.id} id={name.id} avatar={props.store.avatars[0].avatar} />)
    let messageElements = props.store.messages.map(
        message => <MessageItem text={message.text} key={message.id}/>)


    let addDialog = () =>{
        props.onAddDialog();
    };

    let currentDialog = (e) =>{
        let text = e.currentTarget.value;
        props.currentDialogText(text);
    }

    let onSubmit = (newMessage)=>{
        debugger
        props.onAddDialog(newMessage.comment)
        console.log(newMessage)
    }
     //if(!props.isAuth) return <Redirect to={'/login'}/>
    return (
        <div className={classes.dialogItems}>
            <div className={classes.dialog}>
                {nameElements}
            </div>
            <div className={classes.messages}>
                <DialogReduxForm onSubmit={onSubmit}/>
                {messageElements}
            </div>

        </div>
    )
}
let maxLength50 = maxLengthCreator(50)
const DialogForm = (props) =>{

    return(
        <form onSubmit={props.handleSubmit}>
            <div> <Field component={Textarea} name={'comment'} placeholder={'Write your comment'}
            validate={[required, maxLength50 ]}/></div>

            <div>
                <button >Add comment</button>
            </div>
        </form>
    )
}

const DialogReduxForm = reduxForm({form : 'dialog'})(DialogForm)

export default Dialogs;


