import React from "react";
import {profileAPI, usersAPI} from "../API/api";

const ADD_POST = 'ADD-POST'
const UPDATE_POST = 'UPDATE-POST'
const SET_USER_PROFILE = 'SET-USER-PROFILE'
const SET_USER_STATUS = 'SET-USER-STATUS'
const CHANGE_USER_STATUS = 'CHANGE-USER-STATUS'

let initialState = {
    posts: [
        {
            id: 1,
            message: 'Hello guys!!',
            likes: 21,
            avatar: <img
                src={'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQerSvdMpPBNcaYMz9JJQrLR5fAHb04_Ie9f1m8OfOb2ngh0y8W'}
                alt={'avatar'}/>
        },
        {
            id: 2,
            message: 'What are you doing dude?',
            likes: 44,
            avatar: <img
                src={'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQerSvdMpPBNcaYMz9JJQrLR5fAHb04_Ie9f1m8OfOb2ngh0y8W'}
                alt={'avatar'}/>
        },
        {
            id: 3, message: 'Want to check of add some more comment', likes: 89, avatar: <img
                src={'data:image/jpeg;base64,'}
                alt={'avatar'}/>
        }
    ],
    newPostText: '',
    profile: null,
    avatars: [
        {
            id: 1,
            avatar: <img
                src='https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcS4FolXC6aeGZi91crpOff8py7yy45bcY6t2bPZhSK1U3kRsc5b'
                alt={'avatar'}/>
        }
    ],
    userStatus: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let avatar = state.avatars[0].avatar;
            return {
                ...state, posts: [...state.posts,
                    {id: Math.floor(Math.random() * 100), message: action.newPostText, likes: 0, avatar: avatar}]
            }

        }

        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_USER_STATUS: {
            return {...state, userStatus: action.userStatus}
        }
        case CHANGE_USER_STATUS: {
            return {...state, userStatus: action.userStatus}
        }
        default:
            return state;
    }

}
export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setUserStatus = (userStatus) => ({type: SET_USER_STATUS, userStatus})
export const changeUserStatus = (userStatus) => ({type: CHANGE_USER_STATUS, userStatus})


export const getPageOfUserThunkCreator = (UserId) => {
    return (dispatch) => {
        profileAPI.pageOfUser(UserId)
            .then(response => {
                //debugger
                dispatch(setUserProfile(response))
            })
    }
}
export const setUserStatusTC = (userId) => {
    return (dispatch) => {
        profileAPI.setStatusOfUser(userId)
            .then(response => {
                    dispatch(setUserStatus(response.data))
                }
            )
    }
}
export const changeUserStatusTC = (userStatus) => {
    return (dispatch) => {
        profileAPI.changeStatusOfUser(userStatus)
        dispatch(changeUserStatus(userStatus))
    }
}
export const addPostTC = (newPostText) => {
    debugger
    return (dispatch) => {
        dispatch(addPostActionCreator(newPostText))
    }
}


export default profileReducer;