
import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {
    changeUserStatusTC,
    getPageOfUserThunkCreator,
    setUserProfile,
    setUserStatusTC
} from "../../Redux/profile-reducer";
import {Redirect, withRouter} from "react-router-dom";

import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";

class ProfileContainer extends React.Component {

    componentDidMount() {

        let UserId = this.props.match.params.UserId;
        if(!UserId) UserId = 6207;
        this.props.loginThunkCreator(UserId)
        // usersAPI.get(`profile/${UserId}`)
        //     .then(response => {
        //         this.props.setUserProfile(response.data);
        //     });
        this.props.setUserStatusTC(UserId)
    }
    render() {
        //if(!this.props.isAuth)return <Redirect to='/login'/>
        return (
            <Profile {...this.props }  profile = {this.props.profile}
            />
        )
    }
}

 let mapStateToProps = (state) =>({
     profile: state.profilePage.profile,
     status: state.profilePage.userStatus
 });
//let AuthRedirectComponent = withAuthRedirect(ProfileContainer);
// let withUrlDataContainerComponent =  withRouter(AuthRedirectComponent);
// export default connect(mapStateToProps, {loginThunkCreator: getPageOfUserThunkCreator}) (withUrlDataContainerComponent);

export default compose(
    connect(mapStateToProps, {setUserProfile,
        loginThunkCreator: getPageOfUserThunkCreator,
        setUserStatusTC,
        changeUserStatusTC}),
    withRouter,
    withAuthRedirect)(ProfileContainer)