import React from "react";


class ProfileStatus extends React.Component {

    state = {
        editMode: false,
        title: this.props.status
    }
    editModeActive = () => {
        this.setState({editMode: true})
    }
    editModeDeactive = (text) => {
        this.setState({editMode: false})
        this.props.changeUserStatusTC(this.state.title)
    }
    changeStatus = (e) => {
        this.setState({title: e.currentTarget.value})
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.status !== prevProps.status){
            this.setState({title: this.props.status})
        }
    }

    render() {
        //debugger
        return (
            <div>
                {!this.state.editMode ?
                    <div>
                        <span onClick={this.editModeActive}>{this.props.status || 'No Status'}</span>
                    </div> :
                    <div>
                        <input onBlur={this.editModeDeactive} onFocus={true} value={this.state.title} //this.props.status
                               onChange={this.changeStatus} type="text"/>
                    </div>
                }
            </div>
        )
    }
}

export default ProfileStatus