import React from 'react';
import classes from './MyPosts.module.css';
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Textarea} from "../../../common/FormsControl/FormsControls";



const MyPosts = (props) => {

    let postElements = props.store.map(store => <Post message={store.message}  key={Math.floor(Math.random()*100)} likes={store.likes} avatar={store.avatar}
    />);

    const onSubmit = (formData) =>{
        console.log(formData.newPostText)
        props.addPostTC(formData.newPostText);

    }
    return (
        <div className={classes.postItems}>
            <h3>New Post</h3>
            <div>
                <MyPostsReduxForm onSubmit={onSubmit}/>
            </div>
            <div>
                <div>My Posts</div>
                {postElements}
            </div>
        </div>
    );
}

let maxLength30 = maxLengthCreator(30)

const MyPostsForm = (props) =>{
    return <form onSubmit={props.handleSubmit}>

            <Field component={Textarea} name='newPostText'
                   validate={[required, maxLength30]}/>

        <div>
            <button>Add post</button>
        </div>
           </form>
}
const MyPostsReduxForm = reduxForm({form: 'posts'})(MyPostsForm)
export default MyPosts;


